}

.scroll_arrow:hover {
  cursor: pointer;
}

.pushy {
  width: 250px;
  /* Changed the width to 400px */
}

.pushy-left {
  transform: translate3d(-250px, 0, 0);
  /* Updated the values */
  /* Don't forget the vendor prefixes */
}

.pushy-open-left #container,
.pushy-open-left .push {
  transform: translate3d(-250px, 0, 0);
  display: none;
  /* Updated the values */
}

